#whatDo {
    overflow: hidden;
    font-family: 'Raleway', sans-serif;
    padding-top: 60px;
    .whatDoHeading {
        h2 {
            color: #000;
            font-size: 30px;
            letter-spacing: 2px;
            font-weight: 600;
        }
        .underline {
            background: #483290;
            width: 10%;
            height: 2px;
            margin: 0 auto;
            border-radius: 100%;
            -webkit-border-radius: 100%;
            -moz-border-radius: 100%;
            -ms-border-radius: 100%;
            -o-border-radius: 100%;
        }
    }
    .whatLeft {
        .heroSubTitle {
            color: #000;
            letter-spacing: 2px;
        }
        .content {
            h2 {
                color: #115293;
                line-height: 1;
                margin: 20px 0;
                text-transform: capitalize;
                letter-spacing: 2px;
                font-size: 45px;
                font-weight: 700;
            }
            p {
                opacity: .7;
                font-size: 1.1rem;
                color: #000;
                font-weight: 500;
                letter-spacing: 1px;
                transition: .4s;
                -webkit-transition: .4s;
                -moz-transition: .4s;
                -ms-transition: .4s;
                -o-transition: .4s;
                // hover 
                &:hover {
                    opacity: 1;
                    color: #115293;
                }
            }
        }
    }
    .whatRight {
        position: relative;
        img {
            transition: all ease-in-out .5s;
            -webkit-transition: all ease-in-out .5s;
            -moz-transition: all ease-in-out .5s;
            -ms-transition: all ease-in-out .5s;
            -o-transition: all ease-in-out .2s;
        }
        img:hover {
            transform: scale(1.1);
            -webkit-transform: scale(1.1);
            -moz-transform: scale(1.1);
            -ms-transform: scale(1.1);
            -o-transform: scale(1.1);
        }
        .focusImg {
            animation: moving 1.4s infinite alternate-reverse both;
            -webkit-animation: moving 1.4s infinite alternate-reverse both;
        }
        @keyframes moving {
            100% {
                opacity: .8;
            }
        }
    }
}

@media (max-width: 575px) {
    #whatDo {
        text-align: center;
        .whatRight {
            text-align: center;
            img {
                width: 90%;
            }
        }
    }
}